<template>
    <img src="../../assets/about.png" class="about-img" alt="关于我们" />
    <div class="header">
        <div style="display:flex;align-items: center;cursor: pointer;" @click="navigate('/')">
            <img src="../../assets/home.png" class="home-icon"/>
            <span>首页</span>
        </div>
        <span style="margin:0px 10px;"> > </span>
        <span>联系我们</span>
    </div>
    <div class="page">
            <div class="cards">
                <div style="width: 520px;">
                    <div class="name"> {{ info.company_name }}</div>
                    <div class="info" style="margin-top:50px">
                        <img src="../../assets/site1.png" class="icon" />
                        <span style="margin-left: 15px;">固话：{{ info?.contact?.telephone }}</span>
                    </div>
                    <div class="info" style="margin-top:20px">
                        <img src="../../assets/icon1.png" class="icon" />
                        <span style="margin-left: 15px;">电话：{{ info?.contact?.phone }}</span>
                    </div>
                    <div class="info" style="margin-top:20px">
                        <img src="../../assets/icon3.png" class="icon" />
                        <span style="margin-left: 15px;">邮箱：{{ info?.contact?.email }}</span>
                    </div>
                    <div class="info" style="margin-top:20px">
                        <img src="../../assets/icon4.png" class="icon" />
                        <span style="margin-left: 15px;">地址：{{ info?.contact?.address }}</span>
                    </div>
                    <div class="info" style="margin-top:20px">
                        <img src="../../assets/site5.png" class="icon" />
                        <span style="margin-left: 15px;">公司成立时间：{{ info?.contact?.create_company_time }}</span>
                    </div>
                </div>
                <div ref="mapContainer" class="maps"></div>
            </div>
        </div>
</template>
<script setup>
    import { useRouter } from 'vue-router';
    import { computed, ref, onMounted } from "vue";
    import { useStore } from 'vuex';

    const store = useStore();
    const info = computed(() => store.state.info);
    const route = useRouter();
    const navigate = (value) => {
        route.push(value)
    }
    console.log(info)

    const TMap = window.TMap

    let mapContainer = ref(null)

    onMounted(() => {
        let longitude = 116.397590;
        let latitude = 39.908776;
        if (info.value.contact?.longitude) {
            longitude = info.value.contact.longitude;
            latitude = info.value.contact.latitude;
        }
        /* eslint-disable */
        const center = new TMap.LatLng(latitude, longitude)
        const map = new TMap.Map(mapContainer.value, {
            center: center, // 设置地图中心点坐标
            zoom: 15, // 设置地图缩放级别
            viewMode: '2D',
        })
        new TMap.MultiMarker({
            map: map,
            geometries: [{ position: center }],
        })
        /* eslint-enable */
});
    

</script>
