<template>
    <img src="../../assets/about.png" class="about-img" alt="关于我们" />
    <div class="content page-width">
        <div class="header">
            <div style="display:flex;align-items: center;cursor: pointer;" @click="navigate('/')">
                <img src="../../assets/home.png" class="home-icon"/>
                <span>首页</span>
            </div>
            <span style="margin:0px 10px;"> > </span>
            <span>关于我们</span>
        </div>
        <div class="row">
            <div class="left">
                <div class="left-head">
                    <span class="left-name">关于我们</span>
                    <span class="left-en">About us</span>
                </div>
                <div class="left-content" @click="changeCurrent('introduction')">
                    <img v-if="current === 'introduction'" src="../../assets/right-check-icon.png" class="right-icon"/>
                    <img v-else src="../../assets/right-icon.png" class="right-icon"/>
                    <span :class="{ 'left-check': current === 'introduction'}">企业简介</span>
                </div>
                <div class="left-content" @click="changeCurrent('culture')">
                    <img v-if="current === 'culture'" src="../../assets/right-check-icon.png" class="right-icon"/>
                    <img v-else src="../../assets/right-icon.png" class="right-icon"/>
                    <span :class="{ 'left-check': current === 'culture'}">企业文化</span>
                </div>
            </div>
            <div class="right">
                <div>{{ htmls }}</div>
            </div>
        </div>
    </div>
</template>
<script setup>
    import { useRouter, useRoute } from 'vue-router';
    import { computed, ref } from "vue";
    import { useStore } from 'vuex';

    const store = useStore();
    const info = computed(() => store.state.info);
    const route = useRouter();
    const use_route = useRoute();
    let current = ref(use_route.query.type ? use_route.query.type : 'introduction')
    
    let htmls = ref('');
    const navigate = (value) => {
        route.push(value)
    }

    const changeCurrent = (value) => {
        current.value = value;
        if (value === 'introduction') {
            htmls.value = info.value[value]?.about_us
        } else if(value === 'culture'){
            htmls.value = info.value[value]?.mission + info.value[value]?.vision
        } else {
            htmls.value = info.value.introduction?.about_us
        }
    }
    
    changeCurrent(current.value);

</script>
