<template>
    <div class="header">
        <div class="content page-width">
            <div class="logos">
                <img alt="logo" :src="imageUrl" class="logo"/>
                <div class="title">{{ info.company_name }}</div>
            </div>
            <div class="navigate">
                <ul class="rows">
                    <li @click="navigate('/')">首页</li>
                    <li @mouseenter="mouseenter('about')" @mouseleave="mouseleave('about')">
                        关于我们
                        <div class="down" ref="about">
                            <div class="item" @click="navigate('/about', 'introduction')">企业简介</div>
                            <div class="item" @click="navigate('/about', 'culture')">企业文化</div>
                        </div>
                    </li>
                    <li @mouseenter="mouseenter('news')" @mouseleave="mouseleave('news')">
                        新闻中心
                        <div class="down" ref="news">
                            <div class="item" @click="navigate('/companyNews')">公司新闻</div>
                            <div class="item" @click="navigate('/industryNews')">行业资讯</div>
                        </div>
                    </li>
                    <li @mouseenter="mouseenter('product')" @mouseleave="mouseleave('product')">
                        产品中心
                        <div class="down" ref="product">
                            <div class="item" @click="navigate('/product', item.category)" v-for="(item,index) in info.products" :key="index">{{ item.category }}</div>
                        </div>
                    </li>
                    <li @click="navigate('/job')">招聘信息</li>
                    <li @click="navigate('/contactUs')">联系我们</li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script setup>
    import { computed, inject,ref } from "vue";
    import { useStore } from 'vuex';
    import { useRouter } from "vue-router";
    const about = ref(null);
    const news = ref(null);
    const product = ref(null);
    const store = useStore();
    
    const info = computed(() => {
        if(store.state.info?.logo?.logo_url) {
            getImg(store.state.info.logo.logo_url)
        }
        return store.state.info
    });
    const routes = useRouter()
    const cloud = inject('$cloud')
    let imageUrl = ref("");
    const navigate = (value, type) => {
        console.log(type,"12")
        if (type) {
            routes.push({
                path: value,
                query:{
                    type: type
                }
            })
        } else {
            routes.push(value)
        }
        
    }
    console.log(navigate)
    const getImg = async (val) => {
        let res = await cloud.callFunction({
            name: 'pageFunctions',
            data: {
                method:'getTempFileUrl',
                fileList: [val]
            }
        })
        if(res.result.code === 200) {
            imageUrl.value = res.result.data[0].tempFileURL
        }
    }

    const mouseenter = (val) => {
        if (val === "about") {
            about.value.style.height = '100px'
            about.value.style.transition = 'height 0.3s ease';
        } else if(val === "news") {
            news.value.style.height = '100px'
            news.value.style.transition = 'height 0.3s ease';
        } else if(val === "product") {
            product.value.style.height = info.value.products ? info.value.products.length * 50 + 'px' : '0px'
            product.value.style.transition = 'height 0.3s ease';
        }
    }
     const mouseleave = (val) => {
        if (val === "about") {
            about.value.style.height = '0px'
            about.value.style.transition = 'height 0.3s ease';
        } else if(val === "news") {
            news.value.style.height = '0px'
            news.value.style.transition = 'height 0.3s ease';
        } else if(val === "product") {
            product.value.style.height = '0px'
            product.value.style.transition = 'height 0.3s ease';
        }
    }
</script>
<style scoped>
.header{
    background: url('../assets/header.png') 100% 100%;
    height: 60px;
}
.logos{
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    height: 60px;
    margin-top: -13px;
}
.title{
    font-size: 20px;
    font-weight: bold;
}
.content{
    margin: 0px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    width: 48px;
    height: 48px;
    margin-right: 6px;
}
.rows li{
  display: inline-block;
  width: 100px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 18xp;
  cursor: pointer;
  position: relative;
}
.li-check{
    background-color: #0065ab;
    color: #fff;
}
.rows li:hover{
    background-color: #0065ab;
    color: #fff;
}
.down{
    position: absolute;
    z-index: 99;
    background-color: #fff;
    box-shadow: 0px 0px 2px 0px #969595;
    width: 150px;
    top: 60px;
    color: #0065ab;
    overflow: hidden;
    height: 0px;
}
.item{
    border-bottom: 1px solid #999;
    height: 50px;
    line-height: 50px;
}
.item:hover{
    background-color: #4aa6e8;
