<template>
    <img src="../../assets/about.png" class="about-img" alt="关于我们" />
    <div class="content page-width">
        <div class="header">
            <div style="display:flex;align-items: center;cursor: pointer;" @click="navigate('/')">
                <img src="../../assets/home.png" class="home-icon"/>
                <span>首页</span>
            </div>
            <span style="margin:0px 10px;"> > </span>
            <span>产品</span>
        </div>
        <div class="row" v-for="(item,index) in info.products" :key="index">
            <div class="left">
                <div class="left-head">
                    <span class="left-name">{{ item.category }}</span>
                </div>
                <div class="left-content" v-for="(value,num) in item.items" :key="num" @click="item.productsIndex = num">
                    <img v-if="item.productsIndex === num" src="../../assets/right-check-icon.png" class="right-icon"/>
                    <img v-else src="../../assets/right-icon.png" class="right-icon"/>
                    <span :class="{ 'left-check':item.productsIndex === num}">{{ value.product_name }}</span>
                </div>
            </div>
            <div class="right">
                <div v-html="item.items[item.productsIndex].product_description"></div>
            </div>
        </div>
    </div>
</template>
<script setup>
    import { useRouter } from 'vue-router';
    import { computed } from "vue";
    import { useStore } from 'vuex';

    const store = useStore();
    const info = computed(() => {
        if(store.state.info.products) {
            store.state.info.products.forEach(el => {
                el.productsIndex = 0
            });
        }
        return store.state.info
    });
    const route = useRouter();
    const navigate = (value) => {
        route.push(value)
    }

</script>
