<template>
    <div style="border-top: 1px solid #f5f5f5">
        <div class="head">
            <div class="title">招聘岗位:【{{ job.job_title }}】</div>
        </div>
        <div class="content">
            <div class="cards">
                <div class="sm-card">
                    <h6>职位描述</h6>
                    <div class="values" v-for="(item,index) in job.job_description" :key="index">
                        {{ index+1 }}. {{ item }}
                    </div>
                </div>
                <div class="sm-card" style="border-bottom: 0px;padding-top: 20px;">
                    <h6>职位要求</h6>
                    <div class="values" v-for="(item,index) in job.requirements" :key="index">
                        {{ index+1 }}. {{ item }}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
    import { computed} from "vue";
    import { useRoute } from "vue-router";
    import { useStore } from 'vuex';

    const store = useStore();
    const route = useRoute();
    const job = computed(() => {
        if (store.state.info?.recruitment && route.query.type !== undefined) {
            return store.state.info.recruitment[route.query.type] || {}
        }
        return {}
    });
    console.log(job)
</script>
