// store.js
import { createStore } from 'vuex';
import axios from 'axios';

export default createStore({
  state: {
    info: ''
  },
  mutations: {
    setInfo(state, info) {
      state.info = info;
    }
  },
  actions: {
    async fetchInfo({ commit }) {
      let websiteData = {};
      try {
          const domain = window.location.host;
          // const domain = "0af7fd75663ae2550077eef72eb9fe92.ai-site.cc"
          const url = 'http://weka-ai-website-api.ai-site.cc/api/get-website-data?domain=' + domain;
          const response = await axios.get(url);
          const responseData = response?.data;
          let data = JSON.parse(responseData.resp_data);
          if (data[0]) {
            websiteData = data[0]
          }
          document.title = websiteData.company_name
        //   websiteData.routes = [
        //   { path: '/', name: '首页', key: 'home' },
        //   { path: '/product', name: '产品', key: 'product' },
        //   { path: '/about', name: '关于我们', key: 'introduction' }
        // ];
        commit('setInfo', websiteData);
      } catch (error) {
        console.error(error);
      }
    }
  }
});