                <div class="carousel-image" v-for="(image, index) in info?.banners?.banner_urls" :key="index">
                    <img :src="image" alt="Carousel Image" style="width:100vw;height: 100%;"/> 
                </div> 
            </div>
            <img src="../../assets/left-sw.png" @click="previousImage" v-show="currentIndex !== 0" class="left-sw"/>
            <img src="../../assets/right-sw.png" @click="nextImage" v-show="currentIndex !== info?.banners?.banner_urls.length - 1" class="right-sw"/>
        </div>
        <div class="module">
