                            <span class="left-name">{{ item.category }}</span>
                        </div>
                        <div class="left-content" v-for="(value,num) in item.items" :key="num" @click="productsIndex = num">
                            <img v-if="productsIndex === num" src="../../assets/right-check-icon.png" class="right-icon"/>
                            <img v-else src="../../assets/right-icon.png" class="right-icon"/>
                            <span :class="{ 'left-check': productsIndex === num}">{{ value.product_name }}</span>
                        </div>
                    </div>
                    <div class="right">
                        <div v-html="item.items[productsIndex].product_description"></div>
                    </div>
                </div>
            </div>
