                </div>
                <template v-for="(item,index) in info.recruitment" :key="index">
                    <div class="items"  v-if="index < 6" @click="jobDetail(index)">
                        <div class="between">
                            <div class="bet-title">{{ item.job_title }}</div>
                            <div class="look">查看详情 ></div>
                        </div>
                        <div class="xian"><div class="xian-block"></div></div>
                        <div class="description">职位描述</div>
                        <div style="height: 70px;overflow: hidden;">
                            <div class="description-item" v-for="(val,num) in item.job_description" :key="num">
                                {{ num + 1 }}. {{ val }}
                            </div>
                        </div>
                        <div>...</div>
                    
                    </div>
                </template>
            </div>
        </div>
        <div class="module">
            <div class="m-content page-width">
                <div class="m-title" style="margin-bottom: 20px;">
                    <div><span class="title-color">公司</span>新闻</div>
                    <div class="en-title">COMPANY NEWS</div>
                </div>
                <div class="news-card" v-for="(item,index) in info.company_news" :key="index" @click="newsDetail('company_news', index)">
                    <div style="padding-right:29px">
                        <div class="news-year">{{ timeSplit(item.date,0) }}</div>
                        <div class="news-time">{{ timeSplit(item.date,1) }}</div>
                    </div>
                    <div>
                        <div class="between">
                            <div class="new-title">{{ item.title }}</div>
                            <div class="look">查看详情 ></div>
                        </div>
                        <div class="news-content">{{ cutOut(item.content) }}</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="module">
            <div class="m-content page-width">
                <div class="m-title" style="margin-bottom: 20px;">
