    </div>
</template>
<script setup>
    import { ref, onUnmounted } from "vue";
    import { useStore } from 'vuex';
    import { useRouter } from "vue-router";

    const store = useStore();
    const info = store.state.info;
    let productsIndex = ref(0);
    const useter = useRouter();
    const timeSplit = (time, type) =>{
        if (time) {
            let arry = time.split("-");
            if(type === 0) {
                return arry[0]
            } else {
                return `${arry[1]}-${ arry[2] }`
            }
        }
        return ""
    }

    const jobDetail = (index) => {
        useter.push({
            path: '/jobView',
            query: {
                type: index
            }
        })
    }

    const currentIndex = ref(0);  
  
    const previousImage = () => {  
      if (currentIndex.value > 0) {  
        currentIndex.value--;  
      }  
    };  
  
    const nextImage = () => {
      if (currentIndex.value < info.value?.banners?.banner_urls.length - 1) {  
        currentIndex.value++;  
      } else {  
        currentIndex.value = 0; // 如果到达最后一张，则回到第一张  
      }  
    }
    let intervalId = ref(setInterval(nextImage, 3000));
    const mouseleaveSw = () => {
        intervalId.value = setInterval(nextImage, 3000)
    }
    const mouseenterSw = () => {
        clearInterval(intervalId.value);
    }
    onUnmounted(() => {  
      clearInterval(intervalId.value);
    });

    const cutOut = (value) => {
        console.log(value,"123")
        const random = Math.floor(Math.random() * 51 + 150)
        if(value && value.length > random) {
            return value.substring(0,random) + '...'
        }
        return value
    }

    const newsDetail = (type,index) => {
        useter.push({
            path: '/newsDetail',
            query: {
                type,
                index
            }
        })
    }
</script>
