<template>
    <img src="../../assets/about.png" class="about-img" alt="关于我们" />
    <div class="content page-width">
        <div class="header">
            <div style="display:flex;align-items: center;cursor: pointer;" @click="navigate('/')">
                <img src="../../assets/home.png" class="home-icon"/>
                <span>首页</span>
            </div>
            <span style="margin:0px 10px;"> > </span>
            <span>行业资讯</span>
        </div>
        <div class="module">
            <div class="m-content page-width">
                <div class="news-card" v-for="(item,index) in info.industry_news" :key="index" @click="newsDetail('industry_news', index)">
                    <div style="padding-right:29px">
                        <div class="news-year">{{ timeSplit(item.date,0) }}</div>
                        <div class="news-time">{{ timeSplit(item.date,1) }}</div>
                    </div>
                    <div>
                        <div class="between">
                            <div class="new-title">{{ item.title }}</div>
                            <div class="look">查看详情 ></div>
                        </div>
                        <div class="news-content">{{ cutOut(item.content) }}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
    import { useRouter } from 'vue-router';
    import { computed } from "vue";
    import { useStore } from 'vuex';

    const store = useStore();
    const info = computed(() => store.state.info);
    const route = useRouter();
    const navigate = (value) => {
        route.push(value)
    }

    const timeSplit = (time, type) =>{
        if (time) {
            let arry = time.split("-");
            if(type === 0) {
                return arry[0]
            } else {
                return `${arry[1]}-${ arry[2] }`
            }
        }
        return ""
    }

    const cutOut = (value) => {
        const random = Math.floor(Math.random() * 51 + 150)
        if(value && value.length > random) {
            return value.substring(0,random) + '...'
        }
        return value
    }
    const newsDetail = (type,index) => {
        route.push({
            path: '/newsDetail',
            query: {
                type,
                index
            }
        })
    }
</script>
