<template>
    <img src="../../assets/about.png" class="about-img" alt="关于我们" />
    <div class="content page-width">
        <div class="header">
            <div style="display:flex;align-items: center;cursor: pointer;" @click="navigate('/')">
                <img src="../../assets/home.png" class="home-icon"/>
                <span>首页</span>
            </div>
            <span style="margin:0px 10px;"> > </span>
            <span v-if="type === 'company_news'" style="cursor: pointer;" @click="navigate('/companyNews')">公司新闻</span>
            <span v-if="type === 'industry_news'" style="cursor: pointer;" @click="navigate('/industryNews')">行业资讯</span>
            <span style="margin:0px 10px;"> > </span>
            <span>{{ info.title }}</span>
        </div>
        <div class="title">{{ info.title }}</div>
        <div class="time">{{ info.date }}</div>
        <div class="news">{{ info.content }}</div>
    </div>
</template>
<script setup>
    import { useRouter, useRoute } from 'vue-router';
    import { computed } from "vue";
    import { useStore } from 'vuex';

    const store = useStore();
    
    const route = useRouter();
    const currentRoute = useRoute();
    const type = currentRoute.query.type;
    const info = computed(() => {
        if(store.state.info) {
            return store.state.info[type][currentRoute.query.index]
        }
        return {}
    });
    console.log(info)


    const navigate = (value) => {
        route.push(value)
    }

</script>
