// router/index.js
import { createRouter, createWebHistory } from 'vue-router'
// import Home from '../components/Home.vue'
import Home from '../views/Home/indexPage.vue'
import About from '../views/About/aboutPage.vue'
import Product from '../views/Product/productPage.vue'
import CompanyNews from '../views/CompanyNews/indexPage.vue'
import IndustryNews from '../views/IndustryNews/indexPage.vue'
import Job from '../views/Job/indexPage.vue'
import ContactUs from '../views/ContactUs/indexPage.vue'
import JobView from '../views/JobView/indexPage.vue'
import NewsDetail from '../views/NewsDetail/indexPage.vue'
const routes = [
  { path: '/', component: Home },
  { path: '/about', component: About },
  { path: '/product', component: Product },
  { path: '/companyNews', component: CompanyNews },
  { path: '/industryNews', component: IndustryNews },
  { path: '/job', component: Job },
  { path: '/contactUs', component: ContactUs },
  { path: '/jobView', component: JobView },
  { path: '/newsDetail', component: NewsDetail }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router